.header {
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(to right, red 0%, blue 100%);
  /* background: url(""); */
  position: sticky;
  top: 0;
  z-index: 100;
  padding: 0 20px;
}

.header_logo {
  width: 100px;
  height: 50%;
  object-fit: contain;
  margin-top: 12px;
  font-family: cursive;
  text-decoration: none;
  color: white;
}

.header_logo:hover {
  color: black;
}

.header_search {
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;
}

.header_searchInput {
  height: 12px;
  padding: 10px;
  border: none;
  width: 100%;
}

.header_searchIcon {
  padding: 5px;
  height: 22px !important;
  background-color: #cd9042;
}

.header_nav {
  display: flex;
  justify-content: space-evenly;
  margin-left: 25%;
}

.header_option {
  /* height: 250%;   */
  text-decoration: none;
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 70px;
  margin-top: 12.5px;
  color: white;
  align-items: flex-end;
}

.header_option:hover {
  color: red;
}

