.profile-image {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
  cursor: pointer;
}

/* jaidan */
.profile-info {
  position: absolute;
  top: 75px;
  right: 1rem;

  border-radius: 10px;
  display: flex;
  flex-direction: column;
  display: none;
  z-index: 100;
}

.pro-button1 {
  top: 3rem;
  /* background-color: rgb(205, 187, 187); */
  color: white;
  font-weight: bold;
  color: inherit;
}

.pro-button2 {
  top: 10rem;
  /* background-color: rgb(205, 187, 187); */
  color: white;
  font-weight: bold;
  color: inherit;
}

.pro {
  width: 150px;
  height: 50px;
  display: flex;
  color: #fff;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
  transform: background-color 2000;
}

.pro:hover {
  background-color: rgba(0, 0, 0, 0.8);
}
.a-text {
  color: #fff;
}
.a-text:hover {
  color: inherit;
}
